import React, { useEffect, useState } from "react";
import mqtt from "mqtt";

function App() {
  const [message, setMessage] = useState("Waiting for sensor data...");

  useEffect(() => {
    const client = mqtt.connect("ws://localhost:9001");

    client.on("connect", () => {
      console.log("🟢 Connected to MQTT Broker");
      client.subscribe("home/sensors/motion");
    });

    client.on("message", (topic, payload) => {
      console.log(`📩 Message from ${topic}:`, payload.toString());
      setMessage(`Motion Sensor Status: ${payload.toString()}`);
    });

    return () => {
      client.end();
    };
  }, []);

  return (
    <div style={{ textAlign: "center", marginTop: "50px" }}>
      <h1>Real-Time Sensor Monitor</h1>
      <p>{message}</p>
    </div>
  );
}

export default App;
